.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 1200px;
  margin: 40px 0 0 0;
}

.pagesContainer {
  display: flex;
  flex-direction: row;
  background-image: url("./images/wallpaper.jpg");
  background-position: center top;
  height: 600px;
  width: 1200px;
}

.title {
  margin: 40px;
}

.pageItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 450px;
  height: 450px;
  border: solid white;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 0.5);
  color: #474747;
  margin: 15px;
}